
@import "../../inc/vars";

.wp-block-acrobits-softphone-form {
  align-items: center;
  justify-content: center;
  width: 100vw;
  height: 100vh;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 999999;
  background-color: #fff;
  overflow-y: auto;
}

.acrobits_softphone-form {
  height: 100%;
  .gform_ajax_spinner {
    display: block;
    mix-blend-mode: screen;
    filter: brightness(255);
    position: relative;
    top: -35px;
    left: 15px;
  }
  #gform_page_1_2 {
    .gform_ajax_spinner {
      left: calc((100% - 450px)/2 + 15px);
    }
  }
  @media (min-width: $screen-md) and (max-width: 1260px) {
    #gform_page_1_2 {
      .gform_ajax_spinner {
        left: 15px;
      }
    }
  }
  .gform_confirmation_message {
    color: #000;
    font-family: Poppins, sans-serif;
    font-size: 23px;
    font-style: normal;
    font-weight: 400;
    line-height: 156%;
    letter-spacing: -0.35px;
    margin-bottom: 32px;
  }
  &__form {
    padding: 32px 16px 16px;
    flex-grow: 1;
    background-color: #EEEFF2;
  }

  &__content {
    &-wrapper {
      padding: 32px 24px;
      display: flex;
      flex-direction: column;
      justify-content: center;
      height: 100%;
    }
  }

  &__logo {
    max-width: 137px;
    margin: 0 auto 24px;
    img {
      width: 100%;
    }
  }

  &__form-title {
    color: #000;
    text-align: center;
    font-family: Poppins, sans-serif;
    font-size: 29px;
    font-style: normal;
    font-weight: 400;
    line-height: 124%;
    letter-spacing: -0.58px;
    margin-bottom: 28px;
  }


  &_style-1 {
    .acrobits_softphone-form {
      &__content {
        background: url("../../lib/images/blocks/acro-softphone-form/bg/softphone_form_content_style-1.png") no-repeat, linear-gradient(180deg, #3A1F6E 0%, #40237A 33.29%, #5D4292 96.88%);
        background-size: cover;
      }

      &__title {
        color: #fff;
      }

      &__list-title {
        color: #fff;
      }
    }
  }
  &_style-2 {
    .acrobits_softphone-form {
      &__form {
        background-color: #fff;
      }
      &__content {
        background: linear-gradient(180deg, #E9ECF2 0%, #E7E3ED 100%, #E8EAED 100%);
      }

      &__list {
        .list-item {
          &__text {
            color: rgba(0, 0, 1, 0.64);
          }
        }
      }
    }
  }
  &_style-3 {
    .acrobits_softphone-form {
      &__form {
        background-color: #fff;
      }
      &__content {
        background: url("../../lib/images/blocks/acro-softphone-form/bg/softphone_form_content_style-1.png") no-repeat, linear-gradient(180deg, #3A1F6E 0%, #40237A 33.29%, #5D4292 96.88%);
        background-size: cover;

      }

      &__title {
        color: #fff;
        margin-bottom: 20px;
      }

      &__list-title {
        color: #fff;
        margin-bottom: 20px;
      }

      &__list {
        .list-item {
          padding: 12px 0;
          &__image {
            flex-shrink: 0;
            margin-right: 22px;
            border-radius: 50%;
            background-color: #fff;
            width: 72px;
            height: 72px;
            display: flex;
            align-items: center;
            justify-content: center;
            background-color: rgba(0, 0, 0, 0.24);
            &-wrapper {
              width: 32px;
              height: 32px;
            }
          }
        }
      }
    }
  }
  &_style-4 {
    .acrobits_softphone-form {
      &__form {
        background-color: #fff;
      }
      &__content {
        background: linear-gradient(180deg, #E9ECF2 0%, #E7E3ED 100%, #E8EAED 100%);
      }

      &__title {
        margin-bottom: 20px;
      }

      &__list-title {
        margin-bottom: 20px;
      }


      &__list {
        .list-item {
          padding: 12px 0;
          &__text {
            color: rgba(0, 0, 1, 0.64);
          }
          &__image {
            margin-right: 22px;
            border-radius: 50%;
            background-color: #fff;
            width: 72px;
            height: 72px;
            display: flex;
            align-items: center;
            justify-content: center;
            &-wrapper {
              width: 32px;
              height: 32px;
            }
          }
        }
      }
    }
  }


  &__title {
    color: #000;
    font-family: Poppins, sans-serif;
    font-size: 23px;
    font-style: normal;
    font-weight: 400;
    line-height: 156%;
    letter-spacing: -0.35px;
    margin-bottom: 32px;
  }

  &__list-title {
    font-family: Poppins, sans-serif;
    font-size: 18px;
    font-style: normal;
    font-weight: 400;
    line-height: 124%;
    margin-bottom: 11px;
  }

  &__image {
    img {
      max-width: 100%;
      filter: drop-shadow(2px 4px 20px rgba(0,0,0,0.5));
    }
  }

  &__list {
    .list-item {
      display: flex;
      padding: 6px 0;
      color: #fff;
      font-family: Figtree, sans-serif;
      font-size: 15px;
      font-style: normal;
      font-weight: 400;
      line-height: 124%; /* 18.6px */
      align-items: center;
      &__image {
        width: 16px;
        height: 16px;
        margin-right: 12px;
        img {
          width: 100%;
        }
        &-wrapper {
          width: 100%;
          height: 100%;
        }
      }
    }
  }

  // gforms
  .gform_wrapper.gravity-theme .gfield input.medium,
  .gform_wrapper.gravity-theme .gfield select.medium {
    width: 100%;
    border-radius: 4px;
    border: 2px solid #BABFC9;
    color: #8C8698;
    transition: .3s ease all;
    font-size: 23px;
    font-family: Figtree, sans-serif;

    &:hover {
      border-color: #8D95A5;
      color: #635E6E;
    }

    &:focus, &:not(:placeholder-shown) {
      border-color: #1B1D22;
      color: #000;
    }
  }

  .gform_wrapper.gravity-theme {
    .gform_title {
      display: none;
    }
    .gfield_label {
      font-family: Figtree, sans-serif;
      font-size: 20px;
      font-weight: normal;
      margin-bottom: 4px;
    }
    .gfield_required {
      font-size: 16px;
      vertical-align: text-top;
    }
    .gform_fields {
      grid-row-gap: 24px;
    }

    .label-light {
      .gfield_label {
        font-weight: normal;
        font-size: 18px;
      }
    }

    .gfield_checkbox {
      .gchoice {
        padding: 4px 0 4px 4px;
      }
      label {
        font-size: 20px;
        font-family: Figtree, sans-serif;
      }
      .gfield-choice-input {
        &:before {
          content: '';
          border: 1px solid #fff;
          border-radius: 5px;
          width: 16px;
          height: 16px;
          display: block;
          margin-right: 10px;
          transition: .3s ease background-color;
          outline: 4px solid #BABFC9;
        }
        &:checked {
          &:before {
            background-color: #7A50CE;
            outline-color: #7A50CE;
          }
        }
      }


    }

    .gform-theme-button, .gform_button {
      display: flex;
      height: 56px;
      border: none;
      padding: 8px 24px;
      justify-content: center;
      align-items: center;
      border-radius: 10px;
      background-color: #27B989;
      text-align: center;
      font-family: Poppins, sans-serif;
      font-size: 17px;
      font-style: normal;
      font-weight: 500;
      line-height: 100%;
      color: #fff;
      margin: 0 auto;
      cursor: pointer;

      &[type=submit] {
        width: 100%;
        max-width: 450px;
      }
    }

    .booking-form__cal-head {
      color: #000;
      text-align: center;
      font-family: Poppins, sans-serif;
      font-size: 29px;
      font-style: normal;
      font-weight: 400;
      line-height: 124%;
      letter-spacing: -0.58px;
    }

    .gform_previous_button {
      display: none;
    }
    .gform_validation_errors {
      display: none;
    }
  }


  .gform_required_legend {
    display: none;
  }


  // /gforms
}


@media (min-width: $screen-md) {
  .acrobits_softphone-form {

    &_style-1 {
      .acrobits_softphone-form {
        &__content {
          background: url("../../../../lib/images/blocks/acro-softphone-form/bg/softphone_form_content_desktop_style-1.png") no-repeat, var(--grad-popup-bg, linear-gradient(180deg, #3A1F6E 0%, #40237A 33.29%, #5D4292 96.88%));
          background-size: cover;
        }
      }
    }
    &_style-3 {
      .acrobits_softphone-form {
        &__content {
          background: url("../../../../lib/images/blocks/acro-softphone-form/bg/softphone_form_content_desktop_style-1.png") no-repeat, var(--grad-popup-bg, linear-gradient(180deg, #3A1F6E 0%, #40237A 33.29%, #5D4292 96.88%));
          background-size: cover;
          &-wrapper {
            padding-left: 80px;
            padding-right: 80px;
          }
        }
      }
    }

    display: flex;

    &__logo {
      margin: 0;
      max-width: 170px;
      margin-bottom: 32px;
    }

    &__form {
      flex-basis: 41.8%;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding: 32px 36px;
    }

    &__form-title {
      text-align: left;
      line-height: 136%;
      letter-spacing: -0.4px;
      margin-bottom: 0;
    }

    &__content {
      &-wrapper {
        padding: 32px 36px 40px 36px;
      }
      flex-basis: 58.2%;
    }

    &__image {
      height: 34.7%;
      margin-bottom: 25px;

      img {
        height: 100%;
        width: auto;
        margin: 0 auto;
        display: block;
        filter: drop-shadow(2px 4px 33px rgba(0,0,0,0.5));
      }
    }

    &__title {
      font-size: 32px;
      font-weight: 400;
      line-height: 156%;
      letter-spacing: -0.35px;
    }

    &__list-title {
      font-size: 23px;
      line-height: 156%;
      letter-spacing: -0.23px;
    }

    &__list {
      .list-item {
        &__image {
          width: 24px;
          height: 24px;
        }
        &__text {
          padding: 12px 0px;
          font-size: 23px;
          line-height: 124%;
        }
      }
    }

    // gforms
    .gform_wrapper.gravity-theme .gfield input.medium,
    .gform_wrapper.gravity-theme .gfield select.medium {
      padding: 16px 20px;
    }

    .gform_wrapper.gravity-theme {
      .gfield--type-checkbox {
        margin-top: 16px;
      }

      .gfield_label {
        margin-bottom: 8px;
      }

      .gform_page_footer {
        margin-top: 34px;
      }

      .gform-theme-button, .gform_button {
        margin: 0;
        min-width: 245px;
        font-size: 23px;
        font-weight: 500;
        line-height: 100%;

        &[type=submit] {
          margin: 0 auto;
        }
      }

      #gform_page_1_2 {
        .ginput_container_ssa_appointment_field {
          //padding-bottom: 540px;
          //position: relative;
          //margin-bottom: -70px;
        }
        .gfield--type-ssa_appointment {
          iframe {
            //position: absolute;
            //height: 100% !important;
          }
        }
        .gform_button[type=submit] {

        }
      }
    }

  }
}

@media (min-width: $screen-lg) {
  .acrobits_softphone-form {
    height: 100%;
    overflow: hidden;

    &__image {
    }

    &__form {
      padding: 64px 72px;
    }

    &__content {
      &-wrapper {
        padding: 64px 72px 80px 72px;
      }
    }

    &__form-title {
      font-size: 40px;
    }

    &__title {
      font-size: 35px;
    }
  }
}

